.main-nav {
    min-height: 50px;
    width: 100%;
    margin: 0% auto;
    border-top: solid 3px #ef8236;
    box-shadow: 0px 1px 5px #00000033;
    position: fixed;
    z-index: 15;
    top: 0%;
    left: 0%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .navbar {
    height: 100%;
    min-width: 85%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .slide-in-icon {
    display: none;
    transition: 0.2s;
    padding: 5px 6px 3px 6px;
    border-radius: 50%;
    background-color: inherit;
    border: none;
    cursor: pointer;
  }
  
  .navbar .navbar-1 {
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  
  .navbar .navbar-2 {
    display: flex;
    align-items: center;
  }
  
  .nav-logo {
    padding: 5px 25px;
  }
  
  .nav-item {
    
    margin: 0px 3px;
    font-size: small;
    font-weight: 500;
    text-decoration: none;
    color: rgb(69, 69, 69);
    transition: 0.2s;
  }
  
  .nav-btn {
    cursor: pointer;
    border-radius: 20px;
    padding: 10px 20px;
  }
  
  .nav-item:hover,
  .slide-in-icon:hover {
    background-color: rgb(226, 226, 226);
  }
  
  .navbar .navbar-1 form {
    flex-grow: 1;
    padding: 0px 12px;
    position: relative;
    
  }
  
  .navbar .navbar-1 form input {
    width: 600px;
    margin: 0;
    padding: 8px 10px 8px 32px;
    font-size: 13px;
    border: solid 1px #0000003e;
    border-radius: 3px;
  }
  
  .navbar .navbar-1 form .search-icon {
    position: absolute;
    left: 25px;
    top: 8px;
  }
  
  .nav-links {
    padding: 7px 13px;
    border: solid 1px blue;
    border-radius: 3px;
    background-color: #e7f8fe;
    cursor: pointer;
  }
  
  .nav-links:hover {
    background-color: #d3e4eb;
  }
  
  @media screen and (max-width: 1120px) {
    .navbar .navbar-1 form {
      display: none;
    }
  }
  
  @media screen and (max-width: 10600px) {
    .slide-in-icon {
      display: block;
    }
  }
  
  @media screen and (max-width: 620px) {
    .navbar .navbar-1 .res-nav {
      display: none;
    }
  }
  .language-select {
    margin-left: 10px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    cursor: pointer;
  }
  
  